/*-----------------------------------------------------------------------------------*/
/* CSS GLOBAL VARIABLES */
/*-----------------------------------------------------------------------------------*/

:root {

  /* --------- COLOURS --------- */

  --white: #ffffff;
  --black: #3A4145;
  --grey: #ededed;
  --grey-dark: #505050;
  --grey-light: #f6f6f6;
  --accent: #1171ae;
  --accent-light: #73aed3;
  --danger: #b81c23;


  /* --------- GLOBAL --------- */

  --body-font:  'Urbanist';

  --body-bg: var(--black);
  --main-bg: var(--white);
  
  --regular: 300;
  --medium: 500;
  --bold: 700;

}


/*-----------------------------------------------------------------------------------*/
/* SCSS GLOBAL VARIABLES */
/*-----------------------------------------------------------------------------------*/

$theme-name: ddl-v2;

@mixin body-size {
  @include fluid-type(1.4rem, 1.6rem, 1.7rem);
}


/* --------- MARGINS FOR IMAGES, FORMS, TABLES --------- */

$margin-top-lg:      4rem;
$margin-bottom-lg:   4rem;
$margin-top-sm:      2rem;
$margin-bottom-sm:   2rem;